.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2000;
  display: flex;
  justify-content: flex-end;
  animation: fadeIn 0.2s ease-out;
}

.drawer {
  width: 100%;
  max-width: 400px;
  height: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
  animation: slideIn 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  box-shadow: -5px 0 25px rgba(0, 0, 0, 0.1);
}

.header {
  padding: 1.5rem;
  border-bottom: 1px solid var(--border);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header h2 {
  font-size: 1.5rem;
  color: var(--text-main);
  margin: 0;
}

.closeBtn {
  background: none;
  color: var(--text-muted);
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 50%;
  transition: background 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.closeBtn:hover {
  background-color: #f1f2f6;
  color: var(--text-main);
}

.items {
  flex: 1;
  overflow-y: auto;
  padding: 1.5rem;
}

.emptyState {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  color: var(--text-muted);
  gap: 1rem;
}

.emptyIcon {
  color: #ddd;
}

.item {
  display: flex;
  gap: 1rem;
  padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #f1f2f6;
}

.imagePlaceholder {
  width: 80px;
  height: 80px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  flex-shrink: 0;
}

.itemDetails {
  flex: 1;
}

.itemDetails h3 {
  font-size: 1rem;
  margin-bottom: 0.25rem;
  color: var(--text-main);
}

.price {
  font-weight: 700;
  color: var(--primary);
  margin-bottom: 0.75rem;
}

.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.quantity {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background-color: #f8f9fa;
  padding: 0.25rem;
  border-radius: 20px;
  border: 1px solid #eee;
}

.quantity button {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.quantity button:hover {
  background-color: var(--primary);
  color: white;
}

.quantity span {
  font-size: 0.9rem;
  font-weight: 600;
  width: 20px;
  text-align: center;
}

.removeBtn {
  background: none;
  color: #b2bec3;
  padding: 0.5rem;
}

.removeBtn:hover {
  color: #ff7675;
}

.footer {
  padding: 1.5rem;
  border-top: 1px solid var(--border);
  background-color: #f9fafb;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--text-main);
}

.totalAmount {
  font-size: 1.5rem;
  color: var(--primary);
}

.shippingNote {
  font-size: 0.8rem;
  color: var(--text-muted);
  margin-bottom: 1.5rem;
  text-align: center;
}

.checkoutBtn {
  width: 100%;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
  }

  to {
    transform: translateX(0);
  }
}

@media (max-width: 640px) {
  .drawer {
    width: 90%;
    max-width: none;
  }
}
